.toc {
  height: 100vh;
  position: sticky;
  order: 2;
  top: 0;
  overflow-y: auto;
  padding: 15px;
  border-left: 1px solid #dcdcdc;

  ul {
    list-style: none;
    padding-left: 15px;
    font-size: 12.5px;

    li {
      a {
        display: block;
        color: #888;
        text-decoration: none;
        padding: 4px 1px;

        &:hover {
          color: #444;
        }
      }
    }

    &:first-child {
      padding-left: 0;
    }
  }
}
